*,html,body{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
.app{
	height: 100vh;
	background: linear-gradient(45deg, black, transparent);
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	flex-direction: column;
}

.text-container{
	padding: 15px;
	height: 380px;
	width: 40%;
	background: white;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	font-family: 'Redressed', cursive;
	color: firebrick;

}
h1{
	display: flex;
	align-items: center;
}
a{
	color: #e1f9da;
border: 1px solid pink;
padding: 15px;
margin: 20px;
background: linear-gradient( 95deg, #f44336, #03a9f4); 
font-family: fangsong;
font-weight: bold;
}
.mark{
	color: white;
	font-family: 'Dancing Script', cursive;
	animation: changecolor 5s alternate-reverse infinite;
	padding: 7px;
	margin:5px;
	background: #f44336;
}

@keyframes changecolor {
  from {
background: #f44336;
font-size: 1rem;
  }
  to { 
  	background-color: #03a9f4;
  	font-size: 0.5rem;
  }
}

@media(max-width: 850px){
	.text-container{
		width: 70%
	}
}
@media(max-width: 550px){
	.text-container{
		width: 85%
	}
}
